<template>
    <div class="group">
        <app-bar>
            <template>{{name}}</template>
        </app-bar>
        <section class="content">
            <div class="owner-tag" v-if="ownerName">
                <div class="owner-tag__key"></div>
                <div class="owner-tag__value">{{ownerName}}</div>
            </div>
            <div class="group-operation" v-if="!loading">

                <!---->
                <el-alert
                        v-if="!state"
                        title=""
                        type="warning">
                </el-alert>
                <el-button v-else :type="checked?'success':'primary'" :disabled="!state||checked" @click="check">
                    {{checked?'':''}}
                </el-button>
                <br/>


                <el-button type="danger" @click="quitGroup"></el-button>
                <have-not-joined v-if="!hasJoined" :id="id" @hasJoined="hasJoinedHandler"/>
            </div>
            <i class="el-icon-loading" v-else/>
        </section>
        <check-validator ref="checkValidator" :group-id="id"/>
    </div>
</template>

<script>
    import {getGroupInfo, quitGroup} from "../../../../resource/group";
    import AppBar from "../../../../components/AppBar";
    import HaveNotJoined from "./components/HaveNotJoined";
    import {getUserInfo} from "../../../../resource/user";
    import Icon from "../../../../components/Icon";
    import ButtonMore from "../../../../components/ButtonMore";
    import CheckValidator from "./components/CheckValidator";
    import FaceCapture from "./components/FaceCapture";

    export default {
        name: "Group",
        components: {FaceCapture, CheckValidator, ButtonMore, Icon, HaveNotJoined, AppBar},
        props: {
            id: {
                required: true,
                type: String
            }
        },
        created() {
            this.update()
        },
        mounted() {
        },
        data() {
            return {
                ownerId: undefined,
                ownerName: undefined,

                // 
                name: '',

                hasJoined: false,
                // 
                state: false,
                // 
                checked: false,

                loading: false,

                // 
                needFace: false,
                needLocation: false
            }
        },
        watch: {
            id() {
                this.update()
            },
            ownerId() {
                getUserInfo(this.ownerId).then(info => {
                    this.ownerName = info.name
                })
            }
        },
        methods: {
            check() {
                this.$refs.checkValidator.check(this.needLocation, this.needFace)
                    .then(() => {
                        this.update()
                    })
            },
            update() {

                let onSucceedFunc = () => {
                    this.loading = false
                }

                this.loading = true

                getGroupInfo(this.id).then(res => {

                        this.name = res.name
                        this.ownerId = res.owner
                        this.hasJoined = res.role === 1
                        this.state = res.state
                        this.checked = res.checked

                        this.needLocation = res.needLocation
                        this.needFace = res.needFace
                    },
                    e => {
                        return null
                        this.$alert('', '', {
                            confirmButtonText: ''
                        }).then(() => {
                            this.$router.go(-1)
                        })
                    })
                    .then(onSucceedFunc, onSucceedFunc)
            },
            hasJoinedHandler() {
                this.update()
            },
            quitGroup() {
                quitGroup(this.id).then(() => {
                    this.$message('');
                    this.$router.go(-1)
                })
            }
        }
    }
</script>

<style scoped lang="scss">
    @import "../../style/variables";

    .group {
        & > .content {
            position: relative;
            overflow: hidden;
            min-height: calc(100vh - #{$top-bar-height});
        }

        .group-operation {
            position: relative;
            padding: 1em .5em;
        }
        
        .owner-tag{
            border-bottom: 1px solid #409EFF;
            position: relative;
            height: 2em;
            line-height: 2;
            
            .owner-tag__key{
                
                display: inline-block;
                position: relative;
                background-color: #409EFF;
                padding-left: .5em;
                margin-right: 2em;
                
                &:after{
                    content: "";
                    display: inline-block;
                    width: 0px;
                    height: 0px;
                    position: absolute;
                    left: 100%;
                    bottom: 0px;
                    border: 2em solid transparent;
                    border-right-color: #409EFF;
                    transform: translateX(-50%) rotateZ(90deg);
                    z-index: -2;
                }
            }
            .owner-tag__value{
                display: inline-block;
                position: relative;
            }
        }
    }
</style>